import { registerPlugin, mergeConfig } from '@edenx/runtime/plugin';
import { getGlobalLayoutApp, getCurrentEntryName } from '@edenx/runtime/context';

import modernRuntime from '@_edenx_src/edenx.runtime';
const runtimeConfig = typeof modernRuntime === 'function' ? modernRuntime(getCurrentEntryName()) : modernRuntime

const plugins = [];

import { routerPlugin } from '@edenx/runtime/router/internal';

plugins.push(routerPlugin(mergeConfig({"serverBase":["/"]}, (runtimeConfig || {})['router'])));

registerPlugin(plugins, runtimeConfig);
